}

.login-wrapper .left-side form .input-group, .login-wrapper .right-side form .input-group {
  width: 100%;
  position: relative;
  height: 35px;
  margin: 10px 0;
  label-display: block;
}

.login-wrapper .left-side form .input-group input, .login-wrapper .right-side form .input-group input {
  background: #e6e6e6;
  border: none;
  outline-width: 0 !important;
  position: absolute;
  bottom: 0;
  height: 40px;
  padding: 0 10px;
}

.login-wrapper .left-side form .input-group input:focus, .login-wrapper .right-side form .input-group input:focus {
