}

.login-wrapper .left-side form a.right, .login-wrapper .right-side form a.right {
  text-align: right;
  display: block;
}

.login-wrapper .left-side form a:hover, .login-wrapper .right-side form a:hover {
  color: #0069cb;
}

.login-wrapper .left-side form button, .login-wrapper .right-side form button {
  padding: 1em;
  text-align: center;
  border: 0;
  outline-width: 0 !important;
  width: 100%;
  margin: 10px 0;
  position: relative;
  font-weight: 700;
  cursor: pointer;
  color: #333333;
  transition: background-color 200ms linear;
  background-color: #f2f2f2;
}

.login-wrapper .left-side form button:hover, .login-wrapper .right-side form button:hover {
  background-color: #e6e6e6;
}

.login-wrapper .left-side form button.primary, .login-wrapper .right-side form button.primary {
  background-color: #1890ff;
  color: #fff;
}

.login-wrapper .left-side form button.primary:hover, .login-wrapper .right-side form button.primary:hover {
  background-color: #0076e4;
}

.login-wrapper .left-side form button i, .login-wrapper .left-side form button img, .login-wrapper .right-side form button i, .login-wrapper .right-side form button img {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 20px;
  top: 10px;
  line-height: 35px;
}

.title {
  display: none;
}

@media screen and (max-width: 880px) {
  .login-wrapper {
    padding: 0;
  }
  .login-wrapper .left-side {
    display: none;
  }
  .login-wrapper .right-side {
    width: 90%;
