.side-menu {
  position: fixed;
  height: 100vh;
  left: 0;
  width: 200px;
  max-width: 200px;
  background-image: linear-gradient(to bottom, #072c4e, #03121f);
  z-index: 1000;
  transition: max-width 200ms, left 200ms, linear;
}

.side-menu .nav-toggle {
  width: 100%;
  padding: 10px;
  text-align: center;
  background-color: #be0606;
  position: absolute;
  bottom: 0;
  color: #fff;
  font-size: 16px;
  transition: transform 200ms linear;
}

.side-menu.collapsed {
  max-width: 80px;
}

