  padding: 10px 20px;
  cursor: pointer !important;
}

.side-menu .menu-list .menu-item:hover a, .side-menu .menu-list .menu-item:hover i, .side-menu .menu-list .menu-item:hover svg, .side-menu .menu-list .menu-item:hover small {
