.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(5px - 2vmin); */
}

.cardStyle {
  text-align: center;
  width: 200px;
  cursor: pointer;
  margin: 0 20px;
}

.cardStyle.selected {
  border: 3px solid #041d34;
}

.coin-icon {
  font-size: 50px;
}

.coin-icon .fa-bitcoin {
  color: #f2a900;
}
.coin-icon .fa-ethereum {
  color: #5477bb;
}
.coin-icon .fa-hand-holding-usd {
  color: #da5353;
}

.form {
  max-width: 500px;
  margin: 50px auto;
}

.form .fa-exchange-alt {
  line-height: unset;
  padding: 0 10px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Roboto";
}

.settings {
  width: 95%;
  max-width: 800px;
  min-height: 70vh;
  margin: 0 auto;
  box-shadow: 0 0 10px #e6e6e6;
  background-color: white;
  display: flex;
}

.setting-content {
  width: 100%;
  max-width: 600px;
  padding: 2em;
}

h4 {
  text-align: center;
  font-size: 16pt;
  font-weight: 700;
  line-height: 2.5;
}

@media screen and (max-width: 720px) {
  .settings {
    flex-direction: column;
  }
  .setting-content.hide-on-mobile {
    display: none;
  }
}
