* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Roboto";
}

.setting-links {
  padding: 2em;
  background-color: #f7f7f7;
  min-height: 70vh;
}

.setting-links h4 {
  color: lighen(#000, 80);
  font-size: 14pt;
  margin-top: 1em;
}

.setting-links h4 i {
  margin-right: 10px;
  color: #072c4e;
}

.setting-links a {
  margin-left: 2.0em;
  color: #666666;
  font-size: 12pt;
}

.setting-links a:hover {
  color: #1a1a1a;
}

.setting-links a.active {
  color: #072c4e;
}

.setting-links .menu-item.active {
  color: #072c4e !important;
}

@media screen and (max-width: 720px) {
  .setting-links {
    max-width: none;
  }
  .setting-links.hide-on-mobile {
    display: none;
  }
  .setting-links h4 {
    margin-left: 0;
    position: relative;
    left: -20px;
  }
}
