* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Roboto";
}

.switch {
  width: 50px;
  height: 20px;
  border-radius: 10px;
  display: inline-block;
  position: relative;
  background-color: #e6e6e6;
}

.switch.active {
  background-color: #0b467d;
}

.switch.active .switch-toggler {
  background-color: #072c4e;
  border-color: #000;
  box-shadow: 0 0 3px #000;
  left: 30px;
}

.switch .switch-toggler {
  transition: left 200ms linear;
  cursor: pointer;
  display: inline-block;
  content: '';
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background-color: #fff;
  border: 1px solid #cccccc;
}
