* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Roboto";
}

.notification-wrapper {
  display: flex;
  justify-content: space-between;
}

.notification-wrapper > div {
  width: 25%;
}

.notification-wrapper > div:first-of-type {
  width: 35%;
}

.notification-wrapper > div p {
  line-height: 3;
  font-size: 10pt;
}

@media screen and (max-width: 720px) {
  h4 {
    line-height: 1 !important;
  }
  .notification-wrapper > div p {
    font-size: 8pt;
    line-height: 1;
    margin-top: 3em;
  }
}
